import {SelectProperty} from "./SelectProperty/SelectProperty";
import {FormDashboard} from "./FormDashboard/FormDashboard";
import styles from './SearchDashboard.module.css';
import {useState} from "react";
import moment from "moment";

export const SearchDashboard = () => {

  const initialDate = moment();

  const initialState = {
    checkIn: initialDate || '',
    checkOut: null,
    location: null,
  };

  const [state, setState] = useState(initialState);

  const onSetState = () => {

  }

  const onSubmit = () => {
    alert(`location: ${String(state.location)}\nstart: ${state.checkIn}\nend: ${state.checkOut}`)
    console.log(state);
  };

  return (
    <div className={styles.container}>
      <SelectProperty />
      <FormDashboard
        state={state}
        setState={setState}
        onSubmit={onSubmit}
      />
    </div>
  );
};


