import React from "react";
import 'react-dates/initialize';
import styles from './FormDashboardDataItem.module.css';
import bookingIcon from '../../../../assets/icons/booking.png';

export const FormDashboardDataItem = ({
  title = '',
  data = null,
  callback = null,
  isBottomLong = false
}) => {

  return (
    <button
      onClick={callback}
      className={styles.container}
    >
      <div
        style={{
          opacity: isBottomLong ? 0.5 : 0,
          width: `${isBottomLong ? 130 : 0}%`
        }}
        className={styles['bottom-long']}
      ></div>
      <p className={styles.title}>{title}</p>
      <div className={styles.wrapper}>
        <img src={bookingIcon} alt={bookingIcon} />
        <div className={styles['data-wrapper']}>
          <p className={styles['data-show-text']}>{data === null ? 'Select date' : data}</p>
        </div>
      </div>
    </button>
  );
};