import styles from './CarSpecificationsBlock.module.css';
import {Long} from "../../../Common/Long/Long";

export const CarSpecificationsBlock = ({specifications}) => {

  const {
    mileage,
    transmission,
    engine,
  } = specifications;

  return (
    <div className={styles.container}>
      <Long color={'#C9C9C9'}/>
      <div className={styles.wrapper}>
        <div className={styles.block}>
          <span>Mileage (upto)</span>
          <h6>{mileage}</h6>
        </div>
        <Long
          onSize={'50%'}
          color={'#C9C9C9'}
          vertical={true}
        />
        <div className={styles.block}>
          <span>Transmission</span>
          <h6>{transmission}</h6>
        </div>
        <Long
          onSize={'50%'}
          color={'#C9C9C9'}
          vertical={true}
        />
        <div className={styles.block}>
          <span>Engine</span>
          <h6>{engine}</h6>
        </div>
      </div>
    </div>
  );
};