import styles from './ServicesOfferedAdvertising.module.css';
import carRental_image from '../../assets/images/ServicesOfferedAdvertising/carRental.png';
import rentalProperty_image from '../../assets/images/ServicesOfferedAdvertising/rentalProperty.png';
import touring_image from '../../assets/images/ServicesOfferedAdvertising/touring.png';
import {BackGroundSVG} from "./BackGroundSVG";
import {useRef} from "react";
import {useAnimationOnInView} from "../../hooks/useAnimationOnInView";

export const ServicesOfferedAdvertising = () => {

  const CONTAINER = useRef();
  const isInView = useAnimationOnInView(CONTAINER, '-40%');

  return (
    <div className={styles.container} ref={CONTAINER}>
      <BackGroundSVG />
      <div className={styles.wrapper}>
        <div className={styles['left-part']}>
          <svg width="31" height="32" viewBox="0 0 31 32" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M11.5143 10.9817C14.577 7.78422 15.5 0.714355 15.5 0.714355C15.5 0.714355 16.9783 7.77664 19.9286 10.9817C23.1907 14.5255 31 16.3386 31 16.3386C31 16.3386 23.2797 16.8229 19.9286 19.9098C16.2527 23.2958 15.5 31.9628 15.5 31.9628C15.5 31.9628 15.0747 23.3357 11.5143 19.9098C8.11115 16.6353 0 16.3386 0 16.3386C0 16.3386 8.29092 14.3469 11.5143 10.9817Z" fill="#4E4539"/>
          </svg>
          <h2>Services<br/>that we offer?</h2>
          <p>Contrary to popular belief, of classical Latin literature.</p>
        </div>
        <div className={`${styles['right-part-wrapper']} ${isInView && styles['right-part-wrapper-start-in-initial']}`}>
          <div className={`${styles['right-part']} ${isInView && styles['right-part-start-in-initial']}`}>
            {rightPartData.map(({img, h4, content, to}) => (
              <div className={styles['right-item-block']} key={h4}>
                <img src={img} alt={img}/>
                <h4>{h4}</h4>
                <p>{content}</p>
                <button>Explore More →</button>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

const rightPartData = [
  {
    img: rentalProperty_image,
    h4: 'Rental Property',
    content: 'It is a long established fact a reader will be some a random distracted by the readablity content just to add another line to the additional of a page when looking.',
    to: '/',
  },
  {
    img: carRental_image,
    h4: 'Car Rental',
    content: 'Distracted by the readable content of a page when looking at and some of the random text its layout.',
    to: '/',
  },
  {
    img: touring_image,
    h4: 'Touring',
    content: 'It is a long established fact a reader will be some a random distracted by the readablity content just to add another line to the additional of a page when looking.',
    to: '/',
  },
];
