import styles from './FormDashboardLocationItem.module.css';
import Autocomplete from "react-google-autocomplete";
import locationIcon from "../../../../assets/icons/location.png";
import {useState} from "react";
import './castom-autocomplete.css';

export const FormDashboardLocationItem = ({title = ''}) => {

  const [searchData, setSearchData] = useState({ latitude: "", longitude: "" });

  const getLocation = (locData) => {
    const latitude = locData && locData.geometry.location.lat();
    const longitude = locData && locData.geometry.location.lng();

    setSearchData({ latitude, longitude });
  };

  return (
    <div className={styles.container}>
      <p className={styles.title}>{title}</p>
      <div className={styles.wrapper}>
        <img src={locationIcon} alt={locationIcon}/>
        <Autocomplete
          className={styles.input}
          apiKey={"AIzaSyAftYO2yn08-DIdRxUxxFDfSXUtd4QbpII"}
          onPlaceSelected={(place) => {
            getLocation(place);
          }}
        />
      </div>
    </div>
  )
}